import '../sass/style.scss';
import { TodoList } from './list.js';


const inputTaskName = document.getElementById('name');
const selectedPriority = document.getElementById('priority');
const textDetails = document.getElementById('details');
const addButton = document.querySelector('.btn-add');

const todoList = new TodoList();

addButton.addEventListener('click', () => {
    if (!inputTaskName.value.trim()) {
        alert('Please enter a task name');
        return;
    }

    todoList.createNewTodo(
        inputTaskName.value.trim(),
        selectedPriority.value,
        textDetails.value.trim()
    );

    clearForm();
});


function clearForm() {
    inputTaskName.value = '';
    selectedPriority.value = 'pr-1'; 
    textDetails.value = '';
    inputTaskName.focus();
}