export class TodoList {
    constructor() {
        this.listContainer = document.getElementById('list');
    }

    createNewTodo(taskName, priority, details) {
        const currentDate = new Date().toLocaleString();
        const li = document.createElement('li');
        
        li.innerHTML = `
        <div class="list-item">
            <p class="list-item-name">${taskName}</p>
            <p class="list-item-priority">${priority}</p>
            <p class="current-date">${currentDate}</p>
            <button type="button" class="btn-update">Update</button>
            <button type="button" class="btn-delete">Delete</button>
        </div>
        <div class="details-content hidden">
            <p>${details}</p>
        </div>
        `;

        const deleteBtn = li.querySelector('.btn-delete');
        deleteBtn.addEventListener('click', () => this.deleteTodo(li));

        const updateBtn = li.querySelector('.btn-update');
        updateBtn.addEventListener('click', () => this.updateTodo(li));

        this.listContainer.appendChild(li);
    }

    deleteTodo(item) {
        item.remove();
    }

    updateTodo(item) {
        const nameElement = item.querySelector('.list-item-name');
        const priorityElement = item.querySelector('.list-item-priority');
        const detailsElement = item.querySelector('.details-content p');

        console.log('Update todo:', {
            name: nameElement.textContent,
            priority: priorityElement.textContent,
            details: detailsElement.textContent
        });
    }
}